// ========================================================
//
// Fractal theme styles
//
// ========================================================

// Functions
@import "functions/_palette.scss";

// Config
@import "config/_variables.scss";
@import "config/_webfonts.scss";

// Documentation styles
@import "docs/styleguide.scss";

// Theme overrides
a {
  color: $color-link;

  &:hover {
    color: $color-link--hover;
  }

  &:active {
    color: $color-link--active;
  }
}

.Pen-previewLink svg {
  fill: currentColor;
}

.Header {
  background-color: darken(map-get($color-years, year-2016), 30%);
  color: $color-brand--light;
}
