// Import design tokens (from ./src/assets/tokens)
@import 'borders.json';
@import 'breakpoints.json';
@import 'colors.json';
@import 'fonts.json';
@import 'layers.json';

// Color assignments
$color-brand: palette(primary);
$color-brand--light: palette(primary, light);
$color-brand--dark: palette(primary, dark);

$color-neutral: palette(neutral);
$color-neutral--lightest: lighten($color-neutral, 44%);
$color-neutral--lighter: lighten($color-neutral, 24%);
$color-neutral--light: lighten($color-neutral, 8%);
$color-neutral--dark: darken($color-neutral, 8%);
$color-neutral--darker: darken($color-neutral, 24%);
$color-neutral--darkest: darken($color-neutral, 44%);

$color-neutral--alpha-1: rgba($color-neutral, 0.1);
$color-neutral--alpha-2: rgba($color-neutral, 0.2);
$color-neutral--alpha-5: rgba($color-neutral, 0.5);

$color-link: palette(secondary);
$color-link--hover: palette(secondary, dark);
$color-link--active: palette(secondary, light);

$color-text: $color-neutral--darker;

$color-years: (
  year-2005: adjust-hue(palette(primary), -000),
  year-2006: adjust-hue(palette(primary), -004),
  year-2007: adjust-hue(palette(primary), -008),
  year-2008: adjust-hue(palette(primary), -012),
  year-2009: adjust-hue(palette(primary), -016),
  year-2010: adjust-hue(palette(primary), -020),
  year-2011: adjust-hue(palette(primary), -024),
  year-2012: adjust-hue(palette(primary), -028),
  year-2013: adjust-hue(palette(primary), -032),
  year-2014: adjust-hue(palette(primary), -036),
  year-2015: adjust-hue(palette(primary), -040),
  year-2016: adjust-hue(palette(primary), -044),
  year-2017: adjust-hue(palette(primary), -048),
  year-2018: adjust-hue(palette(primary), -052),
  year-2019: adjust-hue(palette(primary), -056),
  year-2020: adjust-hue(palette(primary), -060)
);

$color-days: (
